.box{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap
}
.material-icons{
  font-size: 2.4em;
  cursor: pointer;
}
